const BASE_URL = "http://localhost:8090/departments";
export const department = {
  namespaced: true,
  state: {
    departments: [],
  },
  actions: {
    initState({ commit }) {
      fetch(BASE_URL)
        .then((Response) => Response.json())
        .then((data) => commit("initState", data));
    },

    deleteItem({ state, commit }, index) {
      const ID = state.departments[index].id;
      const URL = BASE_URL + "/" + ID;
      fetch(URL, {
        method: "DELETE",
      }).then(commit("deleteDepartment", index));
    },
  },
  mutations: {
    initState(state, departments) {
      state.departmentsArray = departments;
    },

    deleteDepartment(state, index) {
      state.departments.splice(index, 1);
    },
  },
};
