<template>
  <v-app>
    <v-data-table
      show-select
      :headers="HEADER"
      :items="employees"
      :items-per-page="5"
      class="elevation-1"
    >
      <template v-slot:top>
        <v-toolbar>
          <v-toolbar-title>员工列表</v-toolbar-title>
          <v-divider vertical class="mx-4" inset></v-divider>
          <v-spacer></v-spacer>
          <!--
            v-model is used to control dialog's visibility
            -->
          <v-dialog
            v-model="dialogEdit"
            hide-overlay
            max-width="600px"
            overlay-opacity="true"
          >
            <!--
              v-slot:activator is used to control dialog's  visibility
          -->
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="primary" dark class="mb-2" v-bind="attrs" v-on="on">
                增加员工
              </v-btn>
            </template>
            <EmployeeForm :employee="editedItem" />
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5"
                >Are you sure you want to delete this item?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >取消</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >删除</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <template v-slot:[`item.actions`]="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)"> mdi-pencil </v-icon>
        <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
      </template>
    </v-data-table>
  </v-app>
</template>
<script>
import { employee, employeeHeader } from "../../models/employee.model";
import EmployeeForm from "@/views/employees/EmployeeForm.vue";
import { EventBus } from "@/utils/event-bus";

export default {
  components: { EmployeeForm },
  name: "employees-tab",
  props: {
    employees: Array,
  },
  data() {
    return {
      dialogEdit: false,
      dialogDelete: false,
      editedItem: new employee(),
      editedIndex: -1,
      error: false,
      submitting: false,
      success: false,
      HEADER: employeeHeader,
    };
  },
  mounted() {
    EventBus.$on("saveEmployee", this.save, employee);
    EventBus.$on("cancelEdit", this.close);
  },
  watch: {
    dialogEdit(val) {
      val || this.close();
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  methods: {
    editItem(item) {
      this.editedIndex = this.employees.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogEdit = true;
    },
    save() {
      if (this.editedIndex > -1)
        this.$emit("update-employee", this.editedItem, this.editedIndex);
      else this.$emit("add-employee", this.editedItem);

      this.close();
    },
    close() {
      this.dialogEdit = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, new employee());
        this.editedIndex = -1;
      });
    },

    deleteItem(item) {
      this.editedIndex = this.employees.indexOf(item);
      this.dialogDelete = true;
    },
    deleteItemConfirm() {
      this.$emit("delete-employee", this.editedIndex);
      this.closeDelete();
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    handleSubmit() {
      this.clearStatus();
      this.submitting = true;

      if (this.invalidName || this.invalidEmail) {
        this.error = true;
        return;
      }

      this.$emit("add:employee", this.employee);
      this.$refs.first.focus();
      this.employee = {
        name: "",
        email: "",
      };
      this.success = true;
      this.error = false;
      this.submitting = false;
    },
  },
};
</script>
<style></style>
