<template>
  <div>
    <div class="inline-block">
      <v-data-table
        dense
        dark
        show-select
        :headers="headers"
        :items="dataItems"
        :items-per-page="5"
        class="elevation-1"
      >
        <template v-slot:[`item.ename`]="{ item }">
          {{ item.ename.toUpperCase() }}
        </template>
        <template v-slot:[`item.actions`]="{ item }">
          <v-icon small class="mr-2" @click="editItem(item)">
            mdi-pencil
          </v-icon>
          <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
        </template>
      </v-data-table>
      
    </div>
    <!--
    <div class="inline-block">
      <v-form>
        <v-text-field
          v-model="firstname"
          :rules="rules"
          :counter="10"
          label="部门名称"
          background-color="#FFFFFF"
          color="yellow"
          hint="请输入部门名称"
        ></v-text-field>
        <v-text-field
          v-model="firstname"
          :rules="nameRules"
          :counter="10"
          label="备注"
          required
          background-color="#FFFFFF"
          color="white"
          hint="请输入部门名称"
        ></v-text-field>
        <v-btn class="mr-4" type="submit"> 添加 </v-btn>
        <v-btn class="mr-4" type="submit"> 取消 </v-btn>
      </v-form>
    </div>
    -->
  </div>
</template>
<script>
export default {
  name: "departments-tab",
  props: {
    dataItems: Array,
  },
  data() {
    return {
      enabled: null,
      editing: null,
      error: false,
      submitting: false,
      success: false,
      employee: {
        name: "",
        email: "",
      },

      headers: [
        {
          text: "部门名称",
          align: "start",
          sortable: false,
          value: "ename",
        },
        { text: "备注", value: "phone" },
      ],
    };
  },
  methods: {
    editMode(employee) {
      this.cachedEmployee = Object.assign({}, employee);
      this.editing = employee.id;
    },
    cancelEdit(employee) {
      Object.assign(employee, this.cachedEmployee);
      this.editing = null;
    },
    editEmployee(employee) {
      if (employee.name === "" || employee.email === "") return;
      this.$emit("edit:employee", employee.id, employee);
      this.editing = null;
    },
    handleSubmit() {
      this.clearStatus();
      this.submitting = true;

      if (this.invalidName || this.invalidEmail) {
        this.error = true;
        return;
      }

      this.$emit("add:employee", this.employee);
      this.$refs.first.focus();
      this.employee = {
        name: "",
        email: "",
      };
      this.success = true;
      this.error = false;
      this.submitting = false;
    },
    getColor() {
      return "green";
    },
    isEnabled(slot) {
      return this.enabled === slot;
    },
  },
};
</script>
<style>
/** 
#additem-div {
  position: absolute;
  top: 40%;
  left: 60%;
}
*/
.inline-block {
  display: inline-block;
  margin-right: 15px;
}

#table {
  width: 40%;
}
.form {
  width: 40%;
}
</style>