<template>
  <div id="app">
    <v-app>
      <v-app-bar app>
        <v-icon large color="green darken-2"> mdi-file-account-outline </v-icon>
        <v-toolbar-title>人事管理系统</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn
          v-for="tab in tabs"
          v-bind:key="tab.name"
          v-on:click="currenTab = tab.name"
        >
          {{ tab.label }}
        </v-btn>
        <v-btn icon>
          <v-icon>mdi-export</v-icon>
        </v-btn>
      </v-app-bar>
      <v-main>
        <v-container>
          <component
            v-bind:is="currentTab"
            v-bind="{
              jobs: jobs,
              departments: departments,
              employees: employees,
            }"
            @update-employee="updateEmployee"
            @delete-employee="deleteEmployee"
            @add-employee="addEmployee"
            class="tab"
          ></component>
        </v-container>
      </v-main>
    </v-app>
  </div>
</template>

<script>
import tabItemArray from "./models/tabs.model";
import general from "./views/GeneralTab";
import employees from "./views/employees/EmployeesTab";
import departments from "./views/DepartmentsTab";
import jobs from "./views/JobsTab";
import { mapState } from "vuex";
import {EventBus} from '@/utils/event-bus';
export default {
  name: "App",
  components: {
    general,
    employees,
    departments,
    jobs,
  },
  props: {},

  data() {
    return {
      BASE_URL_EMPLOYEE: "http://localhost:8090/employees",
      employees: [],
      tabs: tabItemArray,
      currentTab: "employees",
    };
  },
  async mounted() {
    this.getEmployees();
    this.$store.dispatch("job/initState");
    this.$store.dispatch("department/initState");
    EventBus.$on('add-employee',this.addEmployee)
    EventBus.$on('update-employee',this.updateEmployee)
    
  },
  computed: {
    ...mapState({
      jobs: (state) => state.job.jobs,
      departments: (state) => state.department.departments,
    }),
  },
  methods: {
    getEmployees() {
      fetch(this.BASE_URL_EMPLOYEE)
        .then((response) => response.json())
       
        .then((data) => (this.employees = data))
        .catch((error) => console.log(error));
    },

    async addEmployee(employee) {
      fetch(this.BASE_URL_EMPLOYEE, {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(employee),
      })
        .then((response) => response.json())
        .then((data) => this.employees.push(data));
    },

    async updateEmployee(employee, index) {
      // const ID = this.employees[index].id;
      //const URL = this.BASE_URL_EMPLOYEE + "/" + ID;
      fetch("http://localhost:8090/employees/5", {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(employee),
      }).then(Object.assign(this.employees[index], employee));
    },

    async deleteEmployee(index) {
      const ID = this.employees[index].id;
      const URL = this.BASE_URL_EMPLOYEE + "/" + ID;
      fetch(URL, {
        method: "DELETE",
      }).then(this.employees.splice(index, 1));
    },
  },
};
</script>

<style></style>
