import Vue from 'vue'
import Vuex from 'vuex'
import App from './App.vue'
import vuetify from './utils/vuetify';
import store from './store';

Vue.config.productionTip = false
Vue.use(Vuex)
//window.store.employees=store.state.employee.employees
window.store=store
new Vue({
  store,
  vuetify,
  render: h => h(App)
}).$mount('#app')

