const BASE_URL = "http://localhost:8090/jobs";
export const job = {
  namespaced: true,
  state: {
    jobs: [],
  },
  /*
  getters: {
    jobs: (state) => {
      return state.jobsArray;
    },
  },
  */
  actions: {
    async initState({ commit }) {
      fetch(BASE_URL)
        .then((response) => response.json())
        .then((data) => commit("initState", data));
    },
  },
  mutations: {
    initState(state, jobs) {
      state.jobs = jobs;
    },
  },
};
